@include font(montserrat-light, montserrat-light);
@include font(gillsans, gillsans);
@include font(gillsans-light, gillsans-light);
@include font(gillsans-bold, gillsans-bold);
@include font(didot, didot);

@mixin ml {
  font-family: 'montserrat-light', sans-serif; }

@mixin g {
  font-family: 'gillsans', sans-serif; }

@mixin gl {
  font-family: 'gillsans-light', sans-serif; }

@mixin gb {
  font-family: 'gillsans-bold', sans-serif; }

@mixin dt {
  font-family: 'didot', serif; }

body {
  min-width: 320px;
  background: #f8f5f7;
  line-height: 1.4; }


.l-center {
  max-width: 1030px;
  margin: 0 auto;
  padding: 0 15px; }


// buttons
.btn {
  display: inline-block;
  padding: 14px 35px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  @include gl;
  font-size: 12px;
  color: #fff;
  background-color: #000;
  border-radius: 2px;
  letter-spacing: 0.05em;
  @include tr(background-color 0.15s ease);
  line-height: 1.2;
  &:hover {
    background-color: gray; }
  &.btn_bold {
    padding: 8px 117px;
    @include gb;
    font-size: 37px;
    @include r(1020) {
      font-size: 30px; }
    @include r(767) {
      padding: 8px 70px;
      font-size: 25px; } } }

.btn-context {
  display: block;
  position: relative;
  width: 20px;
  height: 12px;
  border-top: 2px solid #000;
  border-bottom: 2px solid #000;
  &:before {
    content: "";
    position: absolute;
    top: 50%;
    margin-top: -1px;
    width: 100%;
    height: 2px;
    background: #000; } }


// logo
.logo {
  margin-left: -15px;
  &.logo_center {
    text-align: center;
    margin-left: 0; }
  @include r(1040) {
    margin-left: 0; } }


.logo__link {
  position: relative;
  display: inline-block;
  @include r(1020) {
    max-width: 200px; }
  @include r(500) {
    max-width: 150px; }
  img {
    display: block;
    max-width: 100%; } }

.logo__text {
  position: absolute;
  left: 90%;
  bottom: 10px;
  text-transform: uppercase;
  @include gl;
  font-size: 54px;
  color: #000;
  line-height: 1;
  letter-spacing: 0.05em;
  @include r(1020) {
    bottom: 0;
    font-size: 50px; }
  @include r(500) {
    font-size: 33px; } }


.title-h2 {
  @include gl;
  color: #232323;
  font-size: 72px;
  font-weight: 300;
  letter-spacing: 0.04em;
  @include r(1020) {
    font-size: 65px;
    line-height: 1.1; }
  @include r(767) {
    font-size: 50px; }
  @include r(500) {
    font-size: 40px; } }
